@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,800&display=swap");
@import url("https://fonts.cdnfonts.com/css/common-pixel");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1;
}

html {
  font-size: 62.5%;
  font-family: "Poppins", sans-serif;
  color: black;
}

body {
  background-color: #ffffff;
  /* background-color: #070141;
  background-color: #28232b; */
}

/* .app-section {
} */

.header {
  background-color: #2a274c;
  padding: 1rem 3rem 1rem 3rem;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-items: space-between;

  margin-bottom: 2rem;
}

.search-icon,
.calender-icon,
.heart-icon {
  display: block;
}

.heart-icon {
  margin-top: 0.2rem;
}

.search-input {
  width: 40rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  border-bottom: 1px #ffc903 solid;
  justify-self: start;
}

.search-input input::placeholder {
  font-size: 1.8rem;
  color: grey;
  opacity: 0.9;
}

.search-input input {
  color: #f4f4f4dd;
  border: none;
  background-color: inherit;
  padding: 1rem 0em 0.5rem 0;

  font-size: 2.4rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  text-transform: capitalize;
}
.search-input input:focus {
  outline: none;
}

.main-logo {
  height: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-logo img {
  height: 80%;
}

.darkMode {
  justify-self: end;
}

/* left boc */
.main-section {
  display: grid;
  grid-template-columns:
    minmax(6rem, 1fr) repeat(5, minmax(min-content, 14rem)) minmax(3rem, 5rem) repeat(
      3,
      minmax(min-content, 14rem)
    )
    minmax(6rem, 1fr);
  height: 60rem;
}

.boxR {
  width: 130%;
  grid-column: 8/11;
  border-radius: 9px;
  background-color: #2a274c;
}

.movieList {
  grid-column: 2/7;
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
}

.movieList::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.movieList {
  list-style: none;
  padding: 2rem 1rem;

  height: 100%;
  overflow-y: scroll;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));

  grid-template-rows: 30rem;
  grid-auto-rows: 30rem;

  column-gap: 2rem;
  row-gap: 6rem;
}

.eachMovie {
  border-radius: 9px;
  background-color: #252525;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  overflow: hidden;

  display: grid;
  grid-template-rows: repeat(4, 1fr);
}

.eachMovie-image {
  grid-row: 1/4;
}

.eachMovie img {
  width: 100%;
}

.eachMovie-Details {
  color: #ffffff;
  bottom: 0%;
  width: 100%;

  grid-row: 4/5;

  backdrop-filter: blur(3px);

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  padding: 1rem 1rem 1rem 1.6rem;
}

.eachMovie-Details {
  background: linear-gradient(
    90deg,
    rgb(52 52 52 / 82%) 1%,
    rgba(0, 0, 0, 0.443) 100%
  );
}

.eachMovie-Tittle {
  font-size: 1.4rem;
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  letter-spacing: 1px;
  font-weight: 600;
  grid-column: 1/3;
  line-height: 1.3;
  white-space: nowrap;
  overflow: hidden;
  align-self: center;
}

.eachMovie-year {
  letter-spacing: 1.2px;
  font-size: 1.4rem;
  align-self: center;
}

.eachMovie-Rating {
  font-size: 1.4rem;

  font-weight: 700;
  justify-self: end;
  align-self: center;
  color: black;
  background-color: #ff1010;
  background-color: #ffc903;
  padding: 0.3rem 0.6em;
  border-radius: 50px;
}

.NoImageFound {
  grid-row: 1/4;

  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 1.2rem;
  font-weight: 700;
}

.movieDetails {
  grid-column: 8/11;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr 1fr min-content min-content min-content;
  height: 57rem;

  background-color: #252525;
  padding: 1rem;
  border-radius: 9px;
}

.loader,
.noQuery {
  text-align: center;
  grid-row: 1/6;
  grid-column: 1/5;
  font-size: 2rem;
  font-weight: 800;

  letter-spacing: 1.8px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f5f5f5;
  line-height: 1.7;
}

.moviePoster-Fig {
  grid-column: 1/5;
  grid-row: 1/3;

  border-radius: 9px;
  overflow: hidden;
}

.moviePoster-Fig img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.movieDetails--title {
  font-size: 3rem;
  font-weight: 600;
  line-height: 1.3;
}

.black-overlay {
  grid-row: 1/3;
  grid-column: 1/5;
  -webkit-box-shadow: inset 0px -74px 103px 28px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: inset 0px -74px 103px 28px rgba(0, 0, 0, 0.75);
  box-shadow: inset 0px -74px 103px 28px rgb(0 0 0);

  border-radius: 9px;
  overflow: hidden;
}

.image-overlay-details {
  color: #f5f5f5;
  grid-row: 2/3;
  grid-column: 1/5;

  padding: 2rem 1rem 1rem 2rem;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
}

.movieDetails--rating,
.movieDetails--type,
.movieDetails--rated {
  align-self: flex-start;
  display: flex;
  padding: 0.5rem 1.4rem;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-size: 1.4rem;
  font-weight: 600;
  background-color: #ffc903;
  border-radius: 50px;
  height: 3rem;
  width: 7rem;
}

.movieDetails--Runtime--releasedate {
  font-size: 1.2rem;
  font-weight: 600;

  display: flex;
  gap: 0.8rem;
}

.movieDetails--Runtime--releasedate span {
  font-weight: 500;
}

.movieDetails--genre {
  display: flex;
  gap: 0.5rem;
}

.ind-genre {
  padding: 0.5rem 1rem;
  background-color: #f5f5f542;
  border-radius: 50px;
}

.extra-details {
  grid-column: 1/5;
  grid-row: 4/7;
  padding: 1.4rem 1.2rem;

  display: flex;
  flex-direction: column;
}

.movieDetails--description {
  font-size: 1.4rem;
  line-height: 1.4;

  grid-column: 1/7;
  grid-row: 3/4;
  color: #f5f5f59c;

  margin-bottom: 2rem;
  margin-top: 1.5rem;
}

.movieDetails-director span,
.movieDetails-writer span,
.movieDetails-stars span {
  font-size: 1.4rem;
  font-weight: 600;
  white-space: nowrap;
}

.movieDetails-director,
.movieDetails-writer,
.movieDetails-stars {
  font-size: 1.4rem;
  font-weight: 500;
  display: flex;
  gap: 0.6rem;
}

.movie-imp-team {
  display: flex;
  flex-direction: column;
  grid-column: 1/5;
  color: #f5f5f59c;
  gap: 1.4rem;
}

.bottomDetails {
  color: #f5f5f59c;
  grid-row: 7/8;
}

.bottomDetails {
  display: flex;
  justify-content: space-around;
  /* margin-top: 4rem; */
  color: black;
  margin-top: 1rem;
  grid-column: 1/5;
  padding: 1em 0;
}

.warning-search-for-a-movie {
  grid-column: 1/7;
  height: 58rem;

  display: flex;
  align-items: center;
  justify-content: center;
}
